import React, { useState, useEffect, useRef } from "react";
import { Form } from "react-bootstrap";
import ElemDeleteControl from "./ElemDeleteControlWrapper";
import { StyleSheet } from "aphrodite";
import { CSSPreparation1 } from "../../components/Properties/CSSPreparation1.jsx";
import QuickTools from "../../ReusableComponents/QuickTools/QuickTools";
import { getSuggestedId } from "../../components/IFormDesignerLayout/Logic/UserIdLogic";
import { fromHtmlEntities } from "../../Resources/globalFunctions";
import {
  handleDisplayProperty,
  updateValue,
} from "../../components/IFormDesignerLayout/IFormDesignerLogic";
import SunEditor from "suneditor-react";
import TextEditorToolbarJson from "../../components/Properties/TextEditorToolbarJson";
import "suneditor/dist/css/suneditor.min.css";
import "../css/suneditor-custom.css";
import plugins from "suneditor/src/plugins";
import {
  GetState,
  GetStateFromData,
} from "../../components/CustomHooks/CustomHooks";

const PARAGRAPH = (props) => {
  const contexts = GetStateFromData(props.containerId);
  const { tempCanWidth, controlSelected } = GetState(
    "tempCanWidth",
    "controlSelected"
  );
  var toolbarOptions = [];
  TextEditorToolbarJson["table"] = "false";
  TextEditorToolbarJson["fullScreen"] = "false";
  Object.keys(TextEditorToolbarJson).forEach((key) => {
    if (Array.isArray(TextEditorToolbarJson[key])) {
      let optionsArray = [];
      TextEditorToolbarJson[key].map((subKey) => {
        if (TextEditorToolbarJson[subKey] === "true") {
          optionsArray.push(subKey);
        }
      });
      toolbarOptions.push(optionsArray);
    }
  });
  const toolbarOptionsArray = toolbarOptions;
  const isSubformInPage = props?.isSubformInPage || false;

  const {
    updateJson,
    IdPrecedence,
    deleteControl,
    openQuickToolFor,
    formState2,
    device,
  } = contexts.contexts;
  const conditionalQuickToolOpening =
    props.id === openQuickToolFor ? true : false;

  const { updatedComponentInFormState, removeAndUpdateComponentInFormState } =
    updateJson;

  const data = props.data;
  const actualControlId = props.data.id;
  let mappingId = props.data["style"] && props.data["style"]["MappingId"];
  let userDefinedId = props.data["style"] && props.data["style"]["CustomId"];
  let userDefinedControlId = getSuggestedId(
    actualControlId,
    mappingId,
    userDefinedId,
    IdPrecedence
  );

  const [open, setOpen] = useState(conditionalQuickToolOpening);
  const [mobileDisplay, setMobileDisplay] = useState(true);
  const [tabDisplay, setTabDisplay] = useState(true);
  const startMobile = useRef();
  const endMobile = useRef();
  const startTab = useRef();
  const endTab = useRef();

  useEffect(() => {
    if (formState2 && formState2.style) {
      startMobile.current = parseInt(
        formState2.style?.["Breakpoint"]?.["Mobile"]?.["StartValue"]
      );
      endMobile.current = parseInt(
        formState2.style?.["Breakpoint"]?.["Mobile"]?.["EndValue"]
      );
      startTab.current = parseInt(
        formState2.style?.["Breakpoint"]?.["Tab"]?.["StartValue"]
      );
      endTab.current = parseInt(
        formState2.style?.["Breakpoint"]?.["Tab"]?.["EndValue"]
      );
    }
  }, [
    formState2.style?.["Breakpoint"],
    formState2.style?.["Breakpoint"]?.["Mobile"],
    formState2.style?.["Breakpoint"]?.["Tab"],
  ]);

  useEffect(() => {
    if (props?.previewmode)
      handleDisplayProperty(
        props,
        device,
        setMobileDisplay,
        setTabDisplay,
        startMobile.current,
        endMobile.current,
        startTab.current,
        endTab.current
      );
  }, [
    props?.data?.style?.["MobileVisible"],
    props?.data?.style?.["TabsVisible"],
    device,
    endMobile,
    endTab,
    startMobile,
    startTab,
    tempCanWidth,
  ]);

  useEffect(() => {
    let id1 = document.querySelector(`#${props?.data?.id}_group`);
    if (id1) {
      let id2 = id1.querySelectorAll("div");
      if (id2) {
        // Iterate over the child divs and set attributes
        id2.forEach((div) => {
          // Add your custom attributes here
          div.setAttribute("control_id", `${props?.data?.id}`);
        });
      }
    }
  }, []);

  var jsonForTableHeader = {};
  Object.keys(props.data.style).forEach((keyName) => {
    jsonForTableHeader[keyName] = props.data.style[keyName];
  });
  const preparedStyle1 = CSSPreparation1(jsonForTableHeader);
  var styleSheet = StyleSheet.create({
    styling: preparedStyle1,
  });

  var padding_left = props.data.style && props.data.style["SpacingLeft"];
  if (
    padding_left !== undefined &&
    padding_left !== null &&
    padding_left !== ""
  ) {
  } else {
    padding_left = "0px";
  }

  var padding_right = props.data.style && props.data.style["SpacingRight"];
  if (
    padding_right !== undefined &&
    padding_right !== null &&
    padding_right !== ""
  ) {
  } else {
    padding_right = "0px";
  }

  var padding_top = props.data.style && props.data.style["SpacingTop"];
  if (padding_top !== undefined && padding_top !== null && padding_top !== "") {
  } else {
    padding_top = "0px";
  }

  var padding_bottom = props.data.style && props.data.style["SpacingBottom"];
  if (
    padding_bottom !== undefined &&
    padding_bottom !== null &&
    padding_bottom !== ""
  ) {
  } else {
    padding_bottom = "0px";
  }

  var inputStylePadding = {
    paddingLeft: padding_left,
    paddingRight: padding_right,
    paddingTop: padding_top,
    paddingBottom: padding_bottom,
    display: "block",
    "white-space": "break-spaces",
  };

  const handleContent = (content) => {
    props.data.paragraphText = content?.currentTarget?.innerHTML;
    updateValue(
      props.data.id,
      content?.currentTarget?.innerHTML,
      props.containerId
    );
  };

  if (props.previewmode && props?.data?.style?.Visible === "true") {
    return (
      <Form.Group
        id={userDefinedControlId + "_group"}
        className={" spacingThemeLabel paragraphSunEditor"}
        control_id={userDefinedControlId + "_group"}
        key={data.id}
        elem_type="paragraph"
        frameindex={data.frameIndex}
        elementindex={data.elementIndex}
        sheetIndex={data.sheetIndex}
        sheetElementIndex={data.sheetElementIndex}
        sheetFrameElementindex={data.sheetFrameElementindex}
        style={
          mobileDisplay === false || tabDisplay === false
            ? { display: "none" }
            : inputStylePadding
        }
      >
        <SunEditor
          disable={true}
          hideToolbar={true}
          showToolbar={false}
          enableToolbar={false}
          id={userDefinedControlId}
          control_id={userDefinedControlId}
          lang="en"
          setContents={`${
            props?.data?.paragraphText !== undefined
              ? fromHtmlEntities(props?.data?.paragraphText)
              : ""
          }  `}
          setOptions={{ resizingBar: false, showPathLabel: false }}
        />
      </Form.Group>
    );
  } else {
    return (
      <Form.Group
        id={userDefinedControlId + "_group"}
        className={" spacingThemeLabel paragraphSunEditor"}
        onClick={
          isSubformInPage
            ? () => {}
            : (e) => props.handleSelected(e, props.data.id, "n")
        }
        onKeyDown={isSubformInPage ? () => {} : (e) => props.handleSelected(e)}
        control_id={props.data.id}
        tabIndex="1"
        key={data.id}
        elem_type="paragraph"
        frameindex={data.frameIndex}
        elementindex={data.elementIndex}
        sheetIndex={data.sheetIndex}
        sheetElementIndex={data.sheetElementIndex}
        sheetFrameElementindex={data.sheetFrameElementindex}
        style={inputStylePadding}
      >
        <div
          control_id={props.data.id}
          id={props?.data?.id}
          style={{ border: "1px solid #e4e4e4" }}
        >
          <SunEditor
            id={"paragraphEditor_" + props.id}
            elem_type="paragraph"
            lang="en"
            setOptions={{
              showPathLabel: false,
              plugins: plugins,
              buttonList: toolbarOptionsArray,
            }}
            setContents={props.data.paragraphText}
            onBlur={handleContent}
          />
        </div>
        {controlSelected?.id === props?.id && (
          <ElemDeleteControl
            control_id={props.data.id}
            isSubformInPage={isSubformInPage}
            containerId={props.containerId}
            id={props.data.id}
            data={props.data}
            deleteControl={deleteControl}
            elemType={props.data?.elem_type}
            setDragging={props.setDragging}
            handleReorderStart={props.handleReorderStart}
            handleReorderDrop={props.handleReorderDrop}
            dragId={props.dragId}
            dragType={"column"}
          />
        )}
        {window?.openQuickTools && open && (
          <QuickTools
            containerId={props.containerId}
            selectedControl={props.data}
            open={open}
            setOpen={setOpen}
          />
        )}
      </Form.Group>
    );
  }
};

export default PARAGRAPH;
